@charset "UTF-8";
.StatusTimeLine .timeline__ul {
  list-style-type: none;
  display: flex;
  align-items: center;
  padding: 10px; }

.StatusTimeLine .timeline__li {
  float: left;
  transition: all 200ms ease-in; }

.StatusTimeLine .timeline__li:first-child .timeline__status::after {
  content: "aaa";
  position: absolute;
  color: white;
  background-color: white;
  width: 3.7rem;
  height: 1.5rem;
  border-top: 10px solid white;
  left: 0;
  top: -1.6rem;
  z-index: 54; }

.StatusTimeLine .timeline__li:last-child .timeline__status::after {
  content: "aaa";
  position: absolute;
  color: white;
  background-color: white;
  width: 3.5rem;
  height: 2rem;
  border-top: 10px solid white;
  right: 0;
  top: -1.6rem;
  z-index: 54; }

.StatusTimeLine .timeline__timestamp {
  margin-bottom: 1.4rem;
  padding: 0.2rem 1rem;
  text-align: center;
  font-weight: 100; }

.StatusTimeLine .timeline__content {
  font-weight: 600;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.StatusTimeLine .timeline__tag {
  background: #2196f3;
  color: #ffffff;
  border-radius: 3px;
  font-size: .8rem;
  padding: .1rem .3rem;
  display: inline-flex;
  align-items: center;
  justify-content: center; }

.StatusTimeLine .text-uppercase {
  text-transform: uppercase; }

.StatusTimeLine .timeline__status {
  text-align: center;
  vertical-align: middle;
  padding: 1rem 2.5rem;
  text-align: center;
  font-weight: bolder;
  border-top: 5px solid #d6dce0;
  position: relative;
  transition: all 200ms ease-in; }
  .StatusTimeLine .timeline__status::before {
    content: "";
    color: #fff;
    width: 1.6rem;
    height: 1.6rem;
    background-color: white;
    border-radius: 1.6rem;
    border: 1px solid #ddd;
    position: absolute;
    top: -15px;
    left: 42%;
    transition: all 200ms ease-in;
    z-index: 56; }

.StatusTimeLine .timeline__step {
  font-weight: 500;
  color: #d6dce0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.StatusTimeLine .timeline__li--complete .timeline__status {
  border-top: 5px solid #28a745; }
  .StatusTimeLine .timeline__li--complete .timeline__status::before {
    content: "âœ“";
    color: #fff;
    background-color: #28a745;
    border: none;
    transition: all 200ms ease-in; }

.StatusTimeLine .timeline__li--complete .timeline__step {
  color: black; }

.StatusTimeLine .timeline__li--cancel .timeline__status {
  border-top: 5px dotted #dc3545; }
  .StatusTimeLine .timeline__li--cancel .timeline__status::before {
    content: "X";
    color: #fff;
    background-color: #dc3545;
    border: none;
    transition: all 200ms ease-in; }

.StatusTimeLine .timeline__li--cancel .timeline__step {
  color: black; }

.StatusTimeLine .timeline__li--waiting .timeline__status {
  border-top: 5px solid #ffc107; }
  .StatusTimeLine .timeline__li--waiting .timeline__status::before {
    content: "!";
    color: #fff;
    background-color: #ffc107;
    border: none;
    transition: all 200ms ease-in; }

.StatusTimeLine .timeline__li--waiting .timeline__step {
  color: black; }

.StatusTimeLine .timeline__tag--secondary {
  background-color: #6c757d !important; }

.StatusTimeLine .timeline__tag--success {
  background-color: #28a745 !important;
  color: #ffffff; }

.StatusTimeLine .timeline__tag--warning {
  background-color: #ffc107 !important;
  color: #343a40; }

.StatusTimeLine .timeline__tag--danger {
  background-color: #dc3545 !important;
  color: #fff; }

.StatusTimeLine .timeline__tag--info {
  background-color: #17a2b8 !important;
  color: #fff; }

.StatusTimeLine .timeline__tag--none {
  background: none !important;
  color: #212529; }
